import { Component, OnInit, ViewChild } from '@angular/core';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { Reward } from 'src/models/Reward';
import { PrizeMenuComponent } from './prize-menu/prize-menu.component';
import { ToastText, MenuText } from 'src/commons/Constants';
import { ActionSheetController, PopoverController, ModalController, IonInfiniteScroll } from '@ionic/angular';
import { NewRewardPage } from './new-reward/new-reward.page';
import { PageDataService } from 'src/providers/page-data/page-data.service';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-prize',
  templateUrl: './prize.page.html',
  styleUrls: ['./prize.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class PrizePage {

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "PrizePage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private rewards: Array<Reward> = new Array<Reward>();
  public rewardList: Array<Reward> = new Array<Reward>();
  private searchTerm: string;
  private imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(private actionSheetController: ActionSheetController,
    private modalController: ModalController,
    private popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    private pageDataService: PageDataService) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getRewards();
    this.pageDataService.listUserEvent.subscribe(() => {
      this.imageConcatString = Utils.generateRandomString();
      let data = this.pageDataService.getData('rewardData');
      let editMode = this.pageDataService.getData('rewardEdit');
      this.pageDataService.removeData('rewardData');
      this.pageDataService.removeData('rewardEdit');
      if (!Utils.isVarInvalid(data)) {
        if (!Utils.isVarInvalid(editMode)) {
          this.rewards.splice(this.rewards.indexOf(this.rewards.filter(n => n.getId() == data.getId())[0]), 1);
        }
        this.rewards.push(data);
        this.filter(this.searchTerm);
      }
    });
    this.pageDataService.imageChangeEvent.subscribe(() => {
      this.imageConcatString = Utils.generateRandomString();
    })
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  private getRewards() {
    this.api.getRewards().then((rewards: Array<Reward>) => {
      this.imageConcatString = Utils.generateRandomString();
      this.rewards = rewards;
      this.rewardList = this.rewards;
    }).catch((error) => this.utils.simpleToast(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.rewardList = this.rewards;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.rewardList = this.rewards;
      return;
    }
    this.rewardList = this.rewards.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()) || n.getDescricao().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(reward: Reward) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [
        {
          text: MenuText.EDIT,
          handler: () => {
            this.presentEditPopover(reward);
          }
        }, {
          text: MenuText.DELETE,
          role: 'destructive',
          handler: () => {
            this.api.removeReward(reward).then(() => {
              this.rewards.splice(this.rewards.indexOf(reward), 1);
              this.filter(this.searchTerm)
              this.utils.simpleToast(ToastText.REWARD_REMOVED)
            }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
          }
        },
        {
          text: MenuText.CANCEL, role: 'cancel',
          handler: () => {

          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async presentEditPopover(reward: Reward) {
    const modal = await this.modalController.create({
      component: NewRewardPage,
      backdropDismiss: false,
      componentProps: {
        reward: reward,

      }
    });
    await modal.present();
    let { data } = await modal.onDidDismiss();
    let rewardReturned = Reward.castFromJSON(data);
    if (!Utils.isVarInvalid(data) && this.rewards.indexOf(this.rewards.filter(n => n.getId() == rewardReturned.getId())[0]) >= 0) {
      this.rewards.splice(this.rewards.indexOf(this.rewards.filter(n => n.getId() == rewardReturned.getId())[0]), 1);
      this.rewards.push(rewardReturned);
      this.filter(this.searchTerm);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    this.DismissClick();
    const popover = await this.popoverController.create({
      component: PrizeMenuComponent,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class'
    });
    return await popover.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      this.imageConcatString = Utils.generateRandomString();
      if (this.rewardList.length == 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
